import React from "react";

export default function Navbar() {
  const links = [
    { name: "Home", path: "/customer" },
    { name: "Menu", path: "/customer/menu" },
    { name: "Orders", path: "/customer/orders" },
    { name: "Profile", path: "/customer/profile" },
  ];

  return (
    <header className="fixed top-0 left-0 w-full z-20 py-4 transition-all duration-300 bg-[#3b2b20]/95 backdrop-blur-sm shadow-lg border-b border-amber-200/20">
      <nav className="max-w-7xl mx-auto px-6 flex justify-between items-center text-white">
        {/* Logo Section */}
        <div className="flex items-center space-x-3">
          <div className="w-10 h-10 bg-gradient-to-br from-amber-400 to-amber-600 rounded-full flex items-center justify-center shadow-lg">
            <svg 
              xmlns="http://www.w3.org/2000/svg" 
              width="20" 
              height="20" 
              viewBox="0 0 24 24" 
              fill="none" 
              stroke="currentColor" 
              strokeWidth="2" 
              strokeLinecap="round" 
              strokeLinejoin="round" 
              className="text-white"
            >
              <path d="M17 8h1a4 4 0 1 1 0 8h-1"/>
              <path d="M3 8h14v9a4 4 0 0 1-4 4H7a4 4 0 0 1-4-4Z"/>
              <line x1="6" x2="6" y1="2" y2="4"/>
              <line x1="10" x2="10" y1="2" y2="4"/>
              <line x1="14" x2="14" y1="2" y2="4"/>
            </svg>
          </div>
          <a 
            href="/customer" 
            className="font-bold text-xl tracking-wider hover:text-amber-300 transition-colors duration-200"
          >
            <span className="bg-gradient-to-r from-amber-300 to-amber-500 bg-clip-text text-transparent font-extrabold">
              BIZ
            </span>
            <span className="ml-1 text-white">Nest</span>
            <span className="ml-2 text-amber-200 text-sm font-medium">Cafe</span>
          </a>
        </div>

        {/* Navigation Links */}
        <ul className="hidden md:flex gap-8 uppercase text-sm font-medium">
          {links.map((link) => (
            <li key={link.path}>
              <a
                href={link.path}
                className={`relative px-3 py-2 rounded-lg hover:text-amber-300 transition-all duration-200 hover:bg-white/10 ${
                  typeof window !== 'undefined' && window.location.pathname === link.path 
                    ? "font-extrabold text-amber-300 bg-white/10" 
                    : ""
                }`}
              >
                {link.name}
                {/* Active indicator */}
                {typeof window !== 'undefined' && window.location.pathname === link.path && (
                  <span className="absolute -bottom-1 left-0 right-0 h-0.5 bg-gradient-to-r from-amber-400 to-amber-600 rounded-full"></span>
                )}
              </a>
            </li>
          ))}
        </ul>

        {/* Cart Icon */}
        <div className="flex items-center space-x-4">
          <a 
            href="/customer/orders" 
            aria-label="cart" 
            className="relative p-2 rounded-full hover:bg-white/10 transition-all duration-200 hover:text-amber-300 group"
          >
            <svg 
              xmlns="http://www.w3.org/2000/svg" 
              width="24" 
              height="24" 
              viewBox="0 0 24 24" 
              fill="none" 
              stroke="currentColor" 
              strokeWidth="2" 
              strokeLinecap="round" 
              strokeLinejoin="round" 
              className="lucide lucide-shopping-cart group-hover:scale-110 transition-transform duration-200"
            >
              <circle cx="8" cy="21" r="1" />
              <circle cx="19" cy="21" r="1" />
              <path d="M2.05 2.05h2l2.66 12.42a2 2 0 0 0 2 1.58h9.78a2 2 0 0 0 1.95-1.57l1.65-7.43H5.12" />
            </svg>
            {/* Cart badge (optional - you can add item count here) */}
            <span className="absolute -top-1 -right-1 w-5 h-5 bg-gradient-to-r from-red-500 to-red-600 text-white text-xs rounded-full flex items-center justify-center font-bold shadow-lg opacity-0 group-hover:opacity-100 transition-opacity duration-200">
              0
            </span>
          </a>

          {/* Mobile menu button */}
          <button className="md:hidden p-2 rounded-full hover:bg-white/10 transition-all duration-200">
            <svg 
              xmlns="http://www.w3.org/2000/svg" 
              width="24" 
              height="24" 
              viewBox="0 0 24 24" 
              fill="none" 
              stroke="currentColor" 
              strokeWidth="2" 
              strokeLinecap="round" 
              strokeLinejoin="round"
            >
              <line x1="4" x2="20" y1="12" y2="12"/>
              <line x1="4" x2="20" y1="6" y2="6"/>
              <line x1="4" x2="20" y1="18" y2="18"/>
            </svg>
          </button>
        </div>
      </nav>

      {/* Mobile Navigation Menu (Hidden by default - you can implement toggle functionality) */}
      <div className="md:hidden bg-[#3b2b20]/98 backdrop-blur-sm border-t border-amber-200/20 hidden">
        <ul className="px-6 py-4 space-y-2">
          {links.map((link) => (
            <li key={link.path}>
              <a
                href={link.path}
                className={`block px-4 py-3 rounded-lg uppercase text-sm font-medium hover:text-amber-300 hover:bg-white/10 transition-all duration-200 ${
                  typeof window !== 'undefined' && window.location.pathname === link.path 
                    ? "font-extrabold text-amber-300 bg-white/10" 
                    : ""
                }`}
              >
                {link.name}
              </a>
            </li>
          ))}
        </ul>
      </div>
    </header>
  );
}